// App
import React from "react";

// Router
import { Link } from "react-router-dom";
import Button from "../../Elements/Button";
import Loading from "../../Elements/Loading";
import LoadingFixed from "../../Elements/LoadingFixed";
import CheckBoxSwitcher from "../../Elements/CheckBoxSwitcher";
import api from "../../config/api";
import { connect } from "react-redux";
import { toast } from "react-toastify";
import { bindActionCreators } from "redux";
import { CSSTransitionGroup } from "react-transition-group";
import * as userActions from "../../redux/actions/user";
class Card extends React.Component {
  render() {
    return (
      <div className={`card ${this.props.active ? "active" : ""}`}>
        <div className="row mx-0">
          <div className="col-7 f-20">{this.props.card.maskedPan}</div>
          {this.props.active && (
            <div className="col-5 text-right">
              <span className="left-angle white f-12 mr-0">–ê–∫—Ç–∏–≤–Ω–∞</span>
            </div>
          )}
          {!this.props.active && (
            <div className="col-5 f-12 text-right">
              <span style={{ color: "#B9B9B9" }}>–ù–µ –∞–∫—Ç–∏–≤–Ω–∞</span>
            </div>
          )}
          <div
            className="col-7 f-18"
            style={{
              marginTop: "33px",
            }}
          >
            CARDHOLDER NAME
          </div>
          <div
            className="col-5 text-right f-18"
            style={{
              marginTop: "33px",
            }}
          >
            {this.props.card.expiryDate.slice(4, 6)}/
            {this.props.card.expiryDate.slice(2, 4)}
          </div>
          <div
            className="col"
            style={{
              marginTop: "17px",
            }}
          >
            <div className="card-hidden"></div>
          </div>
          <div
            className="col-12 text-right"
            style={{
              marginTop: "17px",
              height: "38px",
            }}
          >
            <Button
              type="empty"
              paddingVertical="7px"
              className="border-none"
              onClick={() => {
                this.props.removeCard(this.props.card.bindingId);
              }}
            >
              <span className="f-12">–£–¥–∞–ª–∏—Ç—å</span>
            </Button>
            {!this.props.active && (
              <Button
                type="fill"
                paddingVertical="7px"
                className="border-none"
                onClick={() => {
                  this.props.activateCard(this.props.card.bindingId);
                }}
              >
                <span className="f-12">–ê–∫—Ç–∏–≤–∏—Ä–æ–≤–∞—Ç—å</span>
              </Button>
            )}
          </div>
        </div>
      </div>
    );
  }
}
class AutoPay extends React.Component {
  state = {
    enableAutoPay: false,
    isFetching: true,
    bindingIdCard: false,
    cards: false,
  };

  componentDidMount() {
    this.setState({ isFetching: true }, () => {
      fetch(`${api.urlApi}/api/tariffs/getMyAutoPayments`, {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: `Bearer ${this.props.user.apiToken}`,
        },
      })
        .then((response) => response.json())
        .then(({ cards, bindingIdCard, error }) => {
          this.setState({ cards, bindingIdCard, isFetching: false });
        });
    });
  }
  activateCard = (bindingId) => {
    this.setState({ isFetching: true }, () => {
      fetch(`${api.urlApi}/api/tariffs/bindCard`, {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: `Bearer ${this.props.user.apiToken}`,
        },
        body: JSON.stringify({
          bindingId: bindingId,
        }),
      })
        .then((response) => response.json())
        .then(({ errorCode }) => {
          if (!errorCode) {
            toast.success("–ö–∞—Ä—Ç–∞ —É—Å–ø–µ—à–Ω–æ –∞–∫—Ç–∏–≤–∏—Ä–æ–≤–∞–Ω–Ω–∞");
            this.setState({ bindingIdCard: bindingId });
          } else toast.error("–ö–∞—Ä—Ç–∞ –Ω–µ –∞–∫—Ç–∏–≤–∏—Ä–æ–≤–∞–Ω–∞");
          this.setState({ isFetching: false });
        });
    });
  };
  addNew = (e) => {
    e.preventDefault();
    this.setState({ isFetching: true }, () => {
      fetch(`${api.urlApi}/api/tariffs/addNewCard`, {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: `Bearer ${this.props.user.apiToken}`,
        },
      })
        .then((response) => response.json())
        .then(({ response, error }) => {
          if (!error) {
            if (response.formUrl) {
              window.open(response.formUrl);
            } else {
              this.setState({ isFetching: false });
            }
          }
        });
    });
  };
  removeCard = (bindingId) => {
    this.setState({ isFetching: true }, () => {
      fetch(`${api.urlApi}/api/tariffs/removeCard`, {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: `Bearer ${this.props.user.apiToken}`,
        },
        body: JSON.stringify({
          bindingId: bindingId,
        }),
      })
        .then((response) => response.json())
        .then(({ error }) => {
          let state = {};
          if (!error) {
            toast.success("–ö–∞—Ä—Ç–∞ —É—Å–ø–µ—à–Ω–æ —É–¥–∞–ª–µ–Ω–∞");
            if (this.state.bindingIdCard === bindingId)
              state.bindingIdCard = false;
            state.cards = this.state.cards.filter(
              (item) => item.bindingId !== bindingId
            );
          } else toast.error("–ö–∞—Ä—Ç–∞ –Ω–µ —É–¥–∞–ª–µ–Ω–∞");
          this.setState({ isFetching: false, ...state });
        });
    });
  };
  render() {
    return (
      <div className="standart-page">
        <div className="container-fluid">
          <h2 className="title mb-0">–ù–∞—Å—Ç—Ä–æ–π–∫–∏ –∞–≤—Ç–æ–ø–ª–∞—Ç–µ–∂–∞</h2>
          <Link to="/mytarif" className="href-hover f-12">
            –í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –ú–æ–π —Ç–∞—Ä–∏—Ñ
          </Link>
          <div
            className="row mt-4"
            style={{
              marginBottom: "39px",
            }}
          >
            <div className="col d-flex align-items-center">
              <div
                className="mr-4 f-14"
                style={{
                  color: this.props.user.isEnableAutoPay ? "#B9B9B9" : "",
                }}
              >
                –í—ã–∫–ª—é—á–∏—Ç—å
              </div>
              <CheckBoxSwitcher
                val={this.props.user.isEnableAutoPay}
                onChange={() => {
                  this.props.userActions.toogleAutoPay(
                    this.props.user.apiToken
                  );
                }}
              />
              <div
                className="ml-4 f-14"
                style={{
                  color: !this.props.user.isEnableAutoPay ? "#B9B9B9" : "",
                }}
              >
                –í–∫–ª—é—á–∏—Ç—å
              </div>
            </div>
          </div>
          <h3 className="f-16 font-weight-normal">–ü—Ä–∏–≤—è–∑–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç—ã</h3>
          <div className="row card-list">
            <Loading
              isLoading={this.state.isFetching && !this.state.cards}
            ></Loading>
            <LoadingFixed
              isLoading={this.state.isFetching && this.state.cards}
            ></LoadingFixed>
            <CSSTransitionGroup
              transitionName="height-animation-item"
              transitionEnterTimeout={500}
              transitionLeaveTimeout={1}
              style={{
                display: "contents",
              }}
            >
              {this.state.cards && this.props.user.isEnableAutoPay && (
                <>
                  {this.state.cards.map((item, index) => (
                    <Card
                      kay={index}
                      active={item.bindingId === this.state.bindingIdCard}
                      card={item}
                      removeCard={this.removeCard}
                      activateCard={this.activateCard}
                    />
                  ))}
                  <div className="col-12  mt-3 text-center">
                    <Link to="/" onClick={this.addNew} className="href">
                      + –¥–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç—É
                    </Link>
                  </div>
                  <div className="col-12  mt-3  f-12 text-left">
                    –ï—Å–ª–∏ —É –í–∞—Å –ø—Ä–∏–≤—è–∑–∞–Ω–æ –±–æ–ª–µ–µ 1 –∫–∞—Ä—Ç—ã: —á—Ç–æ–±—ã —Å–º–µ–Ω–∏—Ç—å –∫–∞—Ä—Ç—É –¥–ª—è
                    –∞–≤—Ç–æ–ø–ª–∞—Ç–µ–∂–∞ –Ω–∞–∂–º–∏—Ç–µ –Ω–∞ –∫–Ω–æ–ø–∫—É –ê–∫—Ç–∏–≤–∏—Ä–æ–≤–∞—Ç—å –Ω–∞ –Ω–µ–∞–∫—Ç–∏–≤–Ω–æ–π
                    –∫–∞—Ä—Ç–µ.
                  </div>
                </>
              )}
            </CSSTransitionGroup>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};
function mapDispatchToProps(dispatch) {
  return {
    userActions: bindActionCreators(userActions, dispatch),
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(AutoPay);
